.App {
  padding: 80px 80px;
}
.app-header {
  padding: 0;
  margin: 0;
}
.app-header .logo-name {
  position: absolute;
  top: 15px;
  left: 80px;
}
.app-header hr {
  position: relative;
  margin-top: -10px;
}
.app-header img {
  width: 30px;
  position: absolute;
  right: 90px;
  top: 16px;
}
.app-header .cart-number {
  position: absolute;
  top: 30px;
  right: 80px;
  border: 2px;
  color: white;
  background-color: rgba(0, 0, 0, 0.801);
  padding: 2px 5px;
  font-size: 7px;
}
.shopping-cart-item {
  padding: 25px;
  width: 300px;
  max-height: fit-content;
  position: absolute;
  top: 70px;
  right: 80px;
  background: white;
  display: none;
  z-index: 10;
}
.shopping-cart-item .close-icon {
  width: 15px;
  position: absolute;
  right: 10px;
  top: 5px;
}
.shopping-cart-item .shopping-cart-item-details {
  z-index: 10;
}
.shopping-cart-item .shopping-cart-item-details .row {
  margin-bottom: 15px;
}
.shopping-cart-item
  .shopping-cart-item-details
  .row
  .col-7
  .shopping-cart-item-name {
  margin-top: 10px;
  font-size: 12px;
  font-weight: 600;
}
.shopping-cart-item
  .shopping-cart-item-details
  .row
  .col-7
  .shopping-cart-item-price {
  font-size: 15px;
  margin-top: -15px;
}
.shopping-cart-item .shopping-cart-button {
  padding: 5px 50px;
  letter-spacing: 2px;
  color: black;
  background: white;
  width: 100%;
}
.featured-product {
  margin-top: 50px;
  position: relative;
}
.featured-product .featured-product-name {
  margin-bottom: 30px;
  font-weight: 700;
}
.featured-product .featured-product-button {
  position: absolute;
  top: 0;
  right: 0px;
  padding: 10px 50px;
  letter-spacing: 2px;
}
.featured-product .featured-product-image-and-tag {
  position: relative;
}
.featured-product .featured-product-image-and-tag .featured-product-image {
  width: 100%;
  height: 500px;
  position: relative;
}
.featured-product
  .featured-product-image-and-tag
  .featured-product-photo-of-the-day {
  position: absolute;
  bottom: 0px;
  left: 0;
  margin: 0;
  padding: 10px 40px;
  background: white;
}
.featured-product .featured-product-sub-section {
  margin-right: 500px;
}
.featured-product .featured-product-sub-section .featured-product-about {
  position: relative;
  padding: 40px 0px;
}
.featured-product .featured-product-sub-section .featured-product-about h3 {
  font-weight: 700;
}
.featured-product .featured-product-sub-section .featured-product-about h5 {
  margin: 20px 0px;
}
.featured-product .featured-product-people-buy {
  position: absolute;
  right: 0;
  margin: 0;
  padding: 40px 0px;
}
.featured-product .featured-product-people-buy h3 {
  font-weight: 700;
  text-align: right;
  margin-top: 30px;
  margin-bottom: 20px;
}
.featured-product .featured-product-people-buy h3:first-child {
  margin: 0px;
}
.featured-product .featured-product-people-buy .row .col-4 {
  padding: 20px 20px;
}
.featured-product .featured-product-people-buy .row .col-4 img {
  width: 100%;
}
.featured-product .featured-product-people-buy p {
  text-align: right;
}
.featured-product hr {
  margin-top: 90px;
}
.main-page {
  position: relative;
  margin: 0px;
  padding: 40px 0px;
}
.main-page .main-page-h3-1 {
  position: absolute;
  left: -10px;
  margin: 0px;
}
.main-page .main-page-h3-1 span {
  opacity: 0.6;
}
.main-page .main-page-h3-2 {
  position: absolute;
  top: 40px;
  right: 0;
}
.main-page .main-page-h3-2 span {
  margin-right: 10px;
  opacity: 0.6;
}
.main-page-h3-2-mobile {
  display: none;
}
.main-page .row .mainpage-filter-menu {
  position: relative;
  margin: 0;
  padding: 40px 0px;
}
.main-page .row .mainpage-filter-menu h5 {
  margin-top: 40px;
}
.main-page .row .mainpage-filter-menu .category-input {
  display: flex;
  align-items: center;
}
.main-page .row .mainpage-filter-menu .category-input p {
  margin-left: 15px;
  margin-top: 15px;
  text-align: center;
}
.filter-and-close {
  display: none;
}
.mainpage-filter-menu .filter-button {
  display: none;
}

.pagination {
  margin: 15px auto;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination li a {
  opacity: 0.7;
}
.pagination > .active > a {
  color: black;
  font-weight: 700;
  opacity: 1;
}
.pagination > li > a {
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  outline: none;
}
.pagination > li > a,
.pagination > li > span {
  font-size: 30px;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: unset;
}
.pagination-nav-body {
  padding: 40px;
  margin: auto;
}
.pagination-component {
  padding: 20px 0px;
}
.pagination-component .row {
  margin-top: 70px;
  margin-bottom: -40px;
}
.pagination-component .row .col-sm-4 {
  padding: 0px 20px;
  padding-bottom: 20px;
  position: relative;
}
.pagination-component .row .col-sm-4 img {
  width: 100%;
  height: 200px;
}
.pagination-component .products-catelog-catagory {
  text-transform: capitalize;
  font-weight: 1000;
  opacity: 0.6;
}
.pagination-component .products-catalog-title {
  margin-top: -15px;
  font-weight: 700;
}
.pagination-component .products-catalog-price {
  margin-top: -5px;
  opacity: 0.8;
}
.product-catalog-button {
  width: 100%;
}
.product-bestseller {
  position: absolute;
  background: white;
  padding: 2px 10px;
  left: 20px;
  top: 0;
}
.pagination-nav-body .page-active {
  background: black;
  color: white;
}
/*@media screen and (max-width: 900px) and (min-width: 600px) {}*/
@media screen and (max-width: 1100px) {
  .featured-product .featured-product-sub-section {
    margin-right: 300px;
  }
}
@media screen and (max-width: 900px) {
  .featured-product .featured-product-sub-section {
    margin-right: 200px;
  }
}
@media screen and (max-width: 600px) {
  .App {
    padding: 20px;
  }
  .app-header .logo-name {
    top: 20px;
    left: 20px;
  }
  .app-header hr {
    margin-top: 50px;
  }
  .app-header img {
    width: 30px;
    position: absolute;
    right: 30px;
    top: 16px;
  }
  .app-header .cart-number {
    position: absolute;
    top: 30px;
    right: 20px;
  }
  .featured-product {
    margin-top: 20px;
  }
  .shopping-cart-item {
    padding: 25px;
    width: 300px;
    max-height: fit-content;
    position: absolute;
    top: 70px;
    right: 20px;
  }
  .featured-product .featured-product-button {
    top: 570px;
    right: 0px;
    width: 100%;
    padding: 10px 50px;
    letter-spacing: 2px;
  }

  .featured-product .featured-product-sub-section {
    margin-right: 0px;
    padding: 0px 20px;
  }
  .featured-product .featured-product-sub-section .featured-product-about {
    position: relative;
    padding-top: 80px;
  }
  .featured-product .featured-product-sub-section .featured-product-about h3 {
    font-weight: 700;
  }
  .featured-product .featured-product-people-buy {
    position: relative;
    margin: 0;
    padding: 0px;
  }
  .featured-product .featured-product-people-buy h3 {
    text-align: left;
  }
  .featured-product .featured-product-people-buy p {
    text-align: left;
  }
  .featured-product hr {
    margin-top: 30px;
  }
  .main-page-h3-2-desktop {
    display: none;
  }
  .main-page-h3-2-mobile {
    top: 33px;
    display: block;
  }
  .mainpage-filter-menu {
    display: none;
  }
  .main-page .row .mainpage-filter-menu h5 {
    margin-left: 40px;
  }
  .main-page .row .mainpage-filter-menu .category-input {
    display: flex;
    align-items: center;
    margin: 0px;
    margin-left: 40px;
    margin-bottom: -20px;
  }
  .main-page .row .mainpage-filter-menu hr {
    margin-top: 40px;
  }
  .mainpage-filter-menu .filter-and-close {
    display: block;
    position: relative;
  }
  .mainpage-filter-menu .filter-and-close h5 {
    position: relative;
  }
  .mainpage-filter-menu .filter-and-close img {
    position: absolute;
    top: 0;
    right: 0;
    padding-right: 20px;
    width: 50px;
  }
  .mainpage-filter-menu .filter-button {
    display: flex;
    margin: auto;
    background-color: white;
    width: 100%;
    height: 100px;
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
  }
  .mainpage-filter-menu .filter-button .filter-clear-button {
    background: white;
    color: black;
    border: 2px solid black;
    padding: 5px 30px;
    display: block;
    margin: auto;
  }
  .mainpage-filter-menu .filter-button .filter-save-button {
    background: black;
    color: white;
    padding: 5px 30px;
    display: block;
    margin: auto;
  }
}
